<template>
  <h5 class="border-title-box">
    <span class="title">{{title}}</span>
    <slot></slot>
  </h5>
</template>
<script>
export default {
  props: {
    title: {
      type: String,
      default: ''
    }
  }
}
</script>
<style lang="scss" scoped>
.border-title-box {
  position: relative;
  .title{
    margin-left: 10px;
    font-size: 16px;
    color: #333;
    font-weight: 550;
  }
  &:before {
    content: '';
    display: inline-block;
    position: absolute;
    top: 3px;
    width: 4px;
    height: 16px;
    background-image: linear-gradient(
        0deg,
        rgba(49, 107, 255, 0.93) 0%,
        rgba(96, 163, 255, 0.93) 100%
      ),
      linear-gradient(#1e65ff, #1e65ff);
    background-blend-mode: normal, normal;
  }
}
</style>



