import { common } from '../api'
import * as types from './mutation-types'

export default {
  init({ dispatch, commit }) {
    dispatch('getEnum')
  },
  reset({ dispatch }) {
    localStorage.removeItem('muser')
  },
  /**
   * 页面刷新
   */
  refresh({ commit }) {
    if (localStorage.getItem('muser')) {
      let userInfo = JSON.parse(localStorage.getItem('muser'))
      commit(types.USER_INFO, userInfo)
    }
    if (localStorage.getItem('mauths')) {
      let auths = JSON.parse(localStorage.getItem('mauths'))
      commit(types.USER_AUTH, auths)
    }
  },
  /**
   * 获取公共枚举值
   */
  async getEnum({ commit, state }) {
    var exist = new Promise(function(resolve, reject) {
      if (state.enums && state.enums.length) {
        resolve(state.enums)
      }
    })
    return Promise.race([exist, common.getEnum()]).then(rtn => {
      if (rtn.code === 1) {
        commit(types.COM_ENUM, rtn.data)
      }
      return rtn
    })
  }
}



