<template>
  <span class="self-select">
    <input type="text" class="input" :placeholder="placeholder" readonly @focus="togglePanel(true)" v-model="text"/>
    <i :class="['iconf iconfont',!showPanel?'':'rotate',rightIcon]" @click="togglePanel(!showPanel)"></i>
    <transition name="toggle">
      <div class="downlist" v-show="showPanel">
        <slot>
          <ul>
            <li class="option" v-for="(opt,index) in options" v-bind:key="opt.text" @click="select(opt)">{{opt.text}}</li>
          </ul>
        </slot>
      </div>
    </transition>
  </span>
</template>

<script>
export default {
  props: {
    initItems: {
      type: Array,
      default() {
        return []
      }
    },
    displayKey: {
      type: String
    },
    valueKey: {
      type: String
    },
    placeholder: {
      type: String,
      default: '请选择'
    },
    selectedIndex: {
      type: Number,
      default: -1
    },
    initShowPanel: {
      type: Boolean,
      default: false
    },
    blurClose: {
      type: Boolean,
      default: true
    },
    rightIcon: {
      type: String,
      default: 'icon-jiantouarrow483'
    },
    value: null
  },
  data: function() {
    return {
      text: '',
      options: [],
      showPanel: this.initShowPanel
    }
  },
  watch: {
    initItems(curVal, oldVal) {
      this.buildOptions()
    },
    initShowPanel(curVal, oldVal) {
      this.showPanel = curVal
    },
    selectedIndex(curVal, oldVal) {
      if (curVal >= 0 && curVal < this.options.length) {
        this.select(this.options[curVal])
      }
    }
  },
  created() {
    this.buildOptions()
    document.addEventListener('click', this.winClick)
  },
  beforeDestroy() {
    document.removeEventListener('click', this.winClick)
  },
  methods: {
    winClick(event) {
      let target = event.target
      while (target != null && target !== this.$el) {
        target = target.parentNode
      }
      if (!target) {
        this.togglePanel(false)
      }
    },
    togglePanel: function(isShow) {
      this.showPanel = isShow === undefined ? !this.showPanel : isShow
    },
    select(option) {
      if (!option.selected) {
        this.options &&
        this.options.forEach(opt => {
          opt.selected = false
        })
        option.selected = true
        this.text = option.text
        this.$emit('input', option.data)
        this.$emit('change', option.data)
      }
      this.togglePanel(false)
    },
    buildOptions() {
      let opts = []
      if (this.initItems) {
        opts = this.initItems.map(it => {
          let text = this.displayKey ? it[this.displayKey] : it.toString()
          let value = this.valueKey ? it[this.valueKey] : text
          return {
            text: text,
            value: value,
            data: it,
            selected: false
          }
        })
        var option = opts[this.selectedIndex]
        if (option) {
          this.select(option)
        }
      }
      this.options = opts
    }
  }
}
</script>

<style lang="scss" scoped>
.self-select {
  position: relative;
  display: inline-block;
  height: 32px;
  line-height: 32px;
  background-color: #ffffff;
  border-radius: 5px;
  .iconfont {
    position: absolute;
    top: 1px;
    right: 12px;
    color: #ababab;
    cursor: pointer;
    transform: rotate(0deg);
    transition: all 0.5s ease;
    font-size: 12px;
  }
  .iconfont.rotate {
    transform: rotate(180deg);
  }
  .tringle {
    display: block;
    width: 28px;
    height: 31px;
    position: absolute;
    top: 1px;
    right: 5px;
    border-radius: 0 4px 4px 0;
    pointer-events: none;
    &:after {
      content: ' ';
      border: 8px solid transparent;
      border-width: 8px 6px 8px 6px;
      border-top-color: #2881f0;
      position: absolute;
      right: 8px;
      top: 12px;
    }
  }
}

.input {
  width: 106px;
  height: 29px;
  background: #f3f3f3;
  padding: 0 36px 0 13px;
  border-radius: 13px;
  font-size: 14px;
  color: #303030;
  box-sizing: border-box;

  &::-webkit-input-placeholder {
    color: #303030;
  }
}

.downlist {
  position: absolute;
  z-index: 10;
  width: 100%;
  ul {
    border: 1px solid #eee;
    border-radius: 4px;
    background: #fff;
    padding: 3px 0;
    max-height: 480px;
    overflow-y: auto;
  }
}

.option {
  line-height: 24px;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  height: 24px;
  padding: 0 8px;
  color: #444;
  &:hover,
  &.selected {
    color: #fff;
    background: rgba(21, 89, 236, 0.5);
    cursor: pointer;
  }
}

.toggle-enter,
.toggle-leave-active {
  opacity: 0;
  transform: translateY(-10px);
}

.toggle-enter-active,
.toggle-leave-active {
  transition: all ease 0.2s;
}
</style>



