<template>
  <nav class="second-nav">
    <ul class="ul-wrap">
      <router-link class="nav-item" tag="li" :to="item.path?item.path:({name:item.name})" v-for="(item,index) in items" :key="item.tag">
        <span class="item-pre"></span>
        <span class="item-name">{{item.tag}}</span>
        <span class="item-tail"></span>
      </router-link>
    </ul>
  </nav>
</template>
<script>
export default {
  name: 'jSecNav',
  props: {
    items: {
      type: Array,
      default: []
    }
  }
}
</script>
<style lang="scss" scoped>
$borderBottomColor: #cdcdcd;
$borderBottomEmphasisColor: #acc3f5;
$borderWidth: 0 11px 28px 11px;
$bgNormal: #fafafa;
$bgEmphasis: white;
$max: 20;
$gap: -2px;

.second-nav {
  .ul-wrap {
    position: relative;
  }
  .ul-wrap:after {
    content: '';
    position: absolute;
    width: 100%;
    height: 1px;
    background-color: $borderBottomEmphasisColor;
    top: 35px;
    left: 0;
    z-index: 8;
  }
  .nav-item {
    position: relative;
    display: inline-block;
    border-bottom: solid 1px $borderBottomEmphasisColor;
    background-color: $bgNormal;
    z-index: 9;
    cursor: pointer;
  }

  @for $i from 1 to $max {
    .nav-item:nth-child(#{$i}) {
      z-index: 300 - $i;
      transform: translate(($i - 1) * $gap, 0);
    }
  }

  .item-name {
    display: inline-block;
    font-size: 16px;
    padding: 0 20px;
    color: #999;
    line-height: 27px;
    border-top: solid 1px #cdcdcd;
    position: relative;
    z-index: 9;
    font-weight: 500;
  }

  .item-tail,
  .item-pre {
    display: inline-block;
    position: relative;
    width: 0;
    height: 28px;
    z-index: 11;
    left: -7px;
  }
  .item-pre {
    z-index: 10;
  }
  .item-tail {
    height: 28px;
    left: -4px;
  }
  .item-pre:before,
  .item-pre:after {
    content: '';
    position: absolute;
    top: 7px;
    left: 5px;
    border-width: $borderWidth;
    border-style: none solid solid;
    border-color: transparent transparent $borderBottomColor $bgNormal;
  }
  .item-pre:after {
    left: 6px;
    border-color: transparent transparent $bgNormal;
  }
  .item-tail:before,
  .item-tail:after {
    content: '';
    position: absolute;
    top: 7px;
    left: -18px;
    border-width: $borderWidth;
    border-style: none solid solid;
    border-color: transparent $bgNormal $borderBottomColor transparent;
  }
  .item-tail:after {
    left: -19px;
    border-color: transparent transparent $bgNormal;
  }
  .nav-item.selected {
    border-color: white;
    background-color: $bgEmphasis;
    z-index: 301;

    .item-name {
      color: #197aff;
      border-top-color: $borderBottomEmphasisColor;
    }

    .item-pre:before,
    .item-pre:after {
      border-color: transparent transparent $borderBottomEmphasisColor
        $bgEmphasis;
    }
    .item-pre:after {
      left: 6px;
      border-color: transparent transparent $bgEmphasis;
    }
    .item-tail:before,
    .item-tail:after {
      border-color: transparent $bgEmphasis $borderBottomEmphasisColor
        transparent;
    }
    .item-tail:after {
      border-color: transparent transparent $bgEmphasis;
    }
  }
}
</style>



