import http from '../assets/js/request'

export default {
  policyInsert(params) {
    return http.post('/policy/insert', params)
  },
  policyLevel(params) {
    return http.post('/policLevel/selectList', params)
  },
  policyType(params) {
    return http.post('/policyType/selectList', params)
  },
  policySelectList(params) {
    return http.post('/policy/selectList', params)
  },
  policyPageview(params) {
    return http.post('/policy/pageview', params)
  },
  policyDelete(params) {
    return http.post('/policy/delete', params)
  },
  policyUpdate(params) {
    return http.post('/policy/update', params)
  },
  policyDetail(params) {
    return http.post('/policy/selectDetail', params)
  },
  policyUpdateStatus(params) {
    return http.post('/policy/updateStatus', params)
  },
  policyShowStatue(params) {
    return http.post('/policy/showStatue', params)
  }
}



