import http from '../assets/js/request'

export default {
  // 科技一张图
  getTechChart(params) {
    return http.post('/dataMonitor/echMap', params)
  },
  // 科技一张图--点击地图
  getIndicatorData(params) {
    return http.post('/dataMonitor/districtValue', params)
  },
  // 数据监测-国家高新技术企业列表接口
  highNewEnt(params) {
    return http.post('/highNewEnt/selectList', params)
  },
  // 数据监测-科技企业孵化器及众创空间接口
  getInnovationSpace(params) {
    return http.post('/innovationSpace/selectList', params)
  },
  // 数据监测-指标一览,params中type:1,产业发展;2,创新要素;3,企业创新;4,平台载体;5,知识产权
  getIndicatorList(params) {
    return http.post('/dataMonitor/tableData', params)
  },
  // 数据监测-地图+柱状图,params中type:1,产业发展;2,创新要素;3,企业创新;4,平台载体;5,知识产权
  getIndicatorGraphic(params) {
    return http.post('/innovativeMap/variousValue', params)
  },
  // 数据监测-各区域指标变化,params中type:1,产业发展;2,创新要素;3,企业创新;4,平台载体;5,知识产权
  getIndicatorChange(params) {
    return http.post('/dataMonitor/lineData', params)
  }
}



