<template>
  <div class="timeline-wrap clearFix">
    <span :class="['dot-box',idx===curIdx?'active':'']" v-for="(item,idx) in times" :key="item" :style="idx!==times.length-1?liWidth:lastWidth">
      <span class="dot" @click="toggle(idx)"></span>
      <span class="item-name" @click="toggle(idx)">{{item}}</span>
    </span>
  </div>
</template>
<script>
export default {
  props: {
    times: {
      type: Array,
      default() {
        return []
      }
    }
  },
  computed: {
    liWidth() {
      return 'width:calc(' + 100 / (this.times.length - 1) + '%' + ' - 8px)'
    },
    lastWidth() {
      return (
        'width:calc(' +
        100 / (this.times.length - 1) +
        '%' +
        ' - 38px);margin-left:30px;'
      )
    }
  },
  data() {
    return {
      curIdx: this.times.length > 0 ? this.times.length - 1 : 0
    }
  },
  methods: {
    toggle(idx) {
      this.curIdx = idx
      this.$emit('change', { year: this.times[this.curIdx] })
    }
  }
}
</script>
<style lang="scss" scoped>
.timeline-wrap {
  position: relative;
  height: 8px;
  background-color: rgba(197, 212, 227, 0.5);
  border-radius: 3px;

  &:before {
    position: absolute;
    top: 3px;
    left: 12px;
    content: '';
    display: inline-block;
    width: calc(100% - 24px);
    height: 0;
    border-top: dashed 2px #2f97ff;
  }

  .dot-box {
    position: relative;
    display: inline-block;
    cursor: pointer;
    .dot {
      position: absolute;
      content: '';
      left: 15px;
      top: -1px;
      display: inline-block;
      width: 7px;
      height: 7px;
      border-radius: 50%;
      background-color: #fff;
      border: solid 1px #2f97ff;
    }
    &:after {
      position: absolute;
      content: '';
      left: 18.7px;
      top: 8px;
      width: 2px;
      height: 5px;
      background-color: #2f97ff;
    }

    .item-name {
      display: inline-block;
      padding-top: 20px;
      user-select: none;
    }

    &:last-child {
      position: absolute;
      right: 0;
      & .dot {
        left: calc(100% - 19px);
      }
      &:after {
        left: calc(100% - 15px);
      }
      .item-name {
        margin-left: calc(100% - 32px);
      }

      &.active .dot {
        left: calc(100% - 21.5px);
      }
    }

    &.active .dot {
      top: -5px;
      left: 11.5px;
      border-width: 6px;
      width: 4px;
      height: 4px;
    }
  }
}
</style>



