<template>
 <div>
   <el-dialog
     title="打回原因"
     :visible.sync="centerDialogVisible"
     :before-close="clone"
     width="30%">
     <div class="dlBox">
       <p>
         <label>请输入打回原因 :</label>
         <el-input
           type="textarea"
           :rows="6"
           placeholder="请输入内容"
           v-model="textarea"
           maxlength="200">
         </el-input>
       </p>
       <p>
         <label>操作人 :</label>
         <span>
            {{realName}}
          </span>
       </p>
     </div>
     <span slot="footer" class="dialog-footer">
        <el-button round size="small"  type="primary" @click="getCheckedNodesObj">确 定</el-button>
      </span>
   </el-dialog>
 </div>
</template>
<script>
import { dataManage as api } from '../api'
export default {
  name: 'jRepulse',
  props: {
    centerDialogVisible: {
      type: Boolean
    },
    repulseId: {
    }
  },
  data() {
    return {
      textarea: '',
      realName: this.$store.state.user.userInfo.realName
    }
  },
  methods: {
    clone() {
      this.$emit('clone')
    },
    getCheckedNodesObj() {
      let that = this
      let params = {
        reportId: this.repulseId,
        approvalOpinion: this.textarea,
        ispass: 1,
        approvalUserName: this.realName
      }
      if (this.textarea === '') {
        that.$msgbox({
          title: '系统提示',
          message: '<i class="iconfont icon-tanhao"></i><span>' + '打回原因不能为空' + '</span>',
          center: true,
          roundButton: true,
          customClass: 'messageBox',
          type: 'success',
          dangerouslyUseHTMLString: true,
          callback: () => {
          }
        })
        return false
      }
      api.getInfoapply(params).then(res => {
        if (res.code === 1) {
          that.$msgbox({
            title: '系统提示',
            message: '<i class="iconfont icon-correct"></i><span>' + res.msg + '</span>',
            center: true,
            roundButton: true,
            customClass: 'messageBox',
            type: 'success',
            dangerouslyUseHTMLString: true,
            callback: () => {
              this.$emit('repulse')
              this.textarea = ''
            }
          })
        }
      }).catch(err => {
        this.$msgbox({
          title: '系统提示',
          message: '<i class="iconfont icon-tanhao"></i><span>' + err.msg + '</span>',
          center: true,
          roundButton: true,
          customClass: 'messageBox',
          type: 'success',
          dangerouslyUseHTMLString: true,
          callback: () => {
            this.textarea = ''
          }
        })
      })
    }
  }
}
</script>
<style lang="scss" scoped>
  .dlBox{
    p:first-child{
      margin-bottom: 20px;
    }
    &>p{
      width: 100%;
      label{
        display: inline-block;
        margin-bottom: 15px;
      }
    }
  }
  .dialog-footer{
    display: block;
    text-align: center;
  }
</style>



