// 信息模块
import {AUTHS} from '../assets/js/pageAuth'
const adminIndex = r => require.ensure([], () => r(require('@/view/admin/adminIndex')), 'admin')
const adminBase = r => require.ensure([], () => r(require('@/view/admin/adminBase')), 'admin')
const adminPassword = r => require.ensure([], () => r(require('@/view/admin/adminPassword')), 'admin')
export default [
  {
    path: 'admin',
    component: adminIndex,
    meta: {
      auth: AUTHS.adminIndex
    },
    children: [
      {
        name: 'adminBase',
        path: 'adminbase',
        component: adminBase
      },
      {
        name: 'adminPassword',
        path: 'adminpassword',
        component: adminPassword
      },
      {
        path: '/',
        redirect: {name: 'adminBase'}
      }
    ]
  }
]



