<template>
  <div v-if="totalPage>1" class="paging">
    <ul>
      <li v-if="totalPage>7">
        <a :class="['first',{active:pageIndex===1}]" @click="goto(1)">&lt;&lt;</a>
      </li>
      <li :class="pageIndex===1?'opt-gray':''">
        <a @click="goto(pageIndex-1)">上一页</a>
      </li>

      <li v-for="n in pages" :key="n">
        <a :class="{active:n==pageIndex}" @click="goto(n)">{{n}}</a>
      </li>
      <li :class="pageIndex===totalPage?'opt-gray':''">
        <a @click="goto(pageIndex+1)">下一页</a>
      </li>
      <li v-if="totalPage>7">
        <a :class="['last',{active:pageIndex===totalPage}]" @click="goto(totalPage)">&gt;&gt;</a>
      </li>
      <li v-if="mode=='full'" class="skip">
        到第<input type="text" v-model="skipNum" @keyup.enter="goto(skipNum)" />页
        <!--<button @click="goto(skipNum)">确定</button>-->
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  props: {
    initPageSize: {
      type: Number,
      default: 15
    },
    initPageIndex: {
      type: Number,
      default: 1
    },
    initShowPages: {
      type: Number,
      default: 5
    },
    initTotalCount: {
      type: Number,
      default: 0
    },
    initMode: {
      type: String,
      default: 'full'
    }
  },
  data: function() {
    return {
      pageSize: this.initPageSize,
      pageIndex: this.initPageIndex,
      showPages: this.initShowPages,
      mode: this.initMode,
      totalPage: 0,
      pages: [],
      skipNum: ''
    }
  },
  watch: {
    initTotalCount: function(curVal) {
      this.totalCount = curVal
      this.showPages = this.initShowPages // 变更初始总页数后,重置showPages、mode,避免造成...丢失
      this.reset()
      this.getPages()
    },
    initPageIndex: function(curVal) {
      this.pageIndex = curVal
      this.getPages()
    }
  },
  methods: {
    goto: function(page) {
      page = +page
      if (isNaN(page)) return
      if (page < 1 || page > this.totalPage || page === this.pageIndex) return
      this.pageIndex = page
      this.getPages()
      this.$emit('pageChanged', page)
    },
    reset: function() {
      this.totalPage = Math.ceil(this.totalCount / this.pageSize)
      if (this.totalPage <= 1) return
      if (this.showPages >= this.totalPage) {
        this.mode = 'simple'
        this.showPages = this.totalPage
      } else {
        this.mode = 'full'
      }
    },
    getPages: function() {
      let pages = []
      if (this.totalPage > 1) {
        let half = Math.floor(this.showPages / 2)
        let end = Math.min(this.totalPage, this.pageIndex + half)
        let start = Math.max(1, end - this.showPages + 1)
        end = Math.min(this.totalPage, start + this.showPages - 1)
        for (let pi = start; pi <= end; pi++) {
          pages.push(pi)
        }

        if (this.mode === 'full') {
          let diff = this.totalPage - end
          if (diff > 2) {
            pages.push('...', this.totalPage)
          } else {
            for (let i = 1; i <= diff; i++) {
              pages.push(end + i)
            }
            for (let i = -1; i >= diff - 2; i--) {
              if (start + i > 0) {
                // total为6时,出现0页
                pages.unshift(start + i)
              }
            }
          }
        }
      }

      this.pages = pages
    }
  },
  mounted() {
    // 跳转路由后,重新赋值,否则为undefined
    this.totalCount = this.initTotalCount
    this.reset()
    this.getPages()
  }
}
</script>

<style scoped lang="scss">
.paging {
  overflow: hidden;
  ul {
    float: right;
    user-select: none;
  }
  li {
    display: inline-block;
  }
  a {
    color: #333;
    cursor: pointer;
    padding: 0 10px;
    display: block;
    height: 35px;
    min-width: 18px;
    line-height: 35px;
    text-align: center;
    background-color: #fff;
    border-radius: 5px;
    margin-left: 4px;
    font-size: 14px;
    border: solid 1px #ddd;
    &.active {
      color: #5e6c7d;
      background-image: linear-gradient(-23deg, #eaf3ff 0%, #f2f8ff 100%),
        linear-gradient(#f0f0f0, #f0f0f0);
      border-color: #afc9ee;
    }
    &:hover {
      color: #ff9603;
    }
  }
  .opt-gray {
    a {
      color: #333;
      cursor: not-allowed;
      background-color: rgba(221, 221, 221, 0.8);
      opacity: 0.5;
    }
  }
  .skip {
    margin-left: 4px;
    input[type='text'] {
      width: 40px;
      height: 35px;
      border: 1px solid #ddd;
      border-radius: 5px;
      padding: 0 6px;
      margin: 0 2px;
      font-size: 14px;
      text-align: center;
      color: #333;
      &:focus {
        border: solid 1px #409eff;
        outline: none;
      }
    }
    button {
      font-size: 14px;
      border-radius: 5px;
      background: rgba(21, 89, 236, 0.7);
      width: 54px;
      height: 35px;
      color: #fff;
      margin-left: 4px;
      cursor: pointer;
      &:hover {
        background: rgba(21, 89, 236, 0.8);
      }
    }
  }
}
</style>



