let confirmTxt = function(info) {
  return this.$confirm(info, '提示', {
    confirmButtonText: '确定',
    cancelButtonText: '取消',
    center: true,
    roundButton: true,
    customClass: 'messageBox',
    cancelButtonClass: 'is-plain el-button--primary',
    type: 'success'
  })
}
export {
  confirmTxt
}



