<template>
  <div class="login">
    <div class="title">徐州科技大数据运营平台</div>
     <form autocomplete="off">
      <div class="login-new">
        <div class="user-login">登 录</div>
        <div class="uesr-name">
          <i class="iconfont icon-user"></i>
          <input class="username" type="text" placeholder="请输入用户名" v-model="userInfo.username" name="username" @keyup.enter="login">
        </div>
        <p class="errMessage">{{errMessage.username}}</p>
        <div class="uesr-name user-psd">
          <i class="iconfont icon-mima"></i>
          <input class="password" type="password" placeholder="请输入密码" v-model="userInfo.password" name="password" @keyup.enter="login">
        </div>
        <p class="errMessage">{{errMessage.password}}</p>
        <div class="remember-psd">
          <div class="zh fl">
            <label class="remember-psd-sle fl">
            <input type="checkbox" v-model="userInfo.remberAccount">记住账户</label>
          </div>
          <div class="mm fr" @click="forPsd">
            <a class="fr">忘记密码?</a>
          </div>
        </div>
        <p class="error-msg">{{errMessage.loginError}}</p>
        <div class="page-user-login" @click="login">登&nbsp;&nbsp;&nbsp;&nbsp;录</div>
      </div>
    </form>
  </div>
</template>
<script>
export default {
  data() {
    return {
      userInfo: {
        username: '',
        password: '',
        remberAccount: false
      },
      errMessage: {
        username: '',
        password: '',
        loginError: ''
      },
      userType: ''
    }
  },
  mounted() {
    let userInfo = JSON.parse(localStorage.getItem('userInfo'))
    if (userInfo && userInfo.remberAccount) {
      this.userInfo.username = userInfo.username
      this.userInfo.password = userInfo.password
      this.userInfo.remberAccount = userInfo.remberAccount
    } else {
      this.userInfo.username = ''
      this.userInfo.password = ''
      this.userInfo.remberAccount = false
    }
  },
  methods: {
    usenamefocus() {
      this.errMessage.loginError = ''
    },
    passwordfocus() {
      this.errMessage.loginError = ''
    },
    validityFn() {
      if (!this.userInfo.username) {
        this.errMessage.username = '用户名不能为空'
        return false
      }
      if (!this.userInfo.password) {
        this.errMessage.password = '密码不能为空'
        return false
      }
      return true
    },
    login() {
      if (!this.validityFn()) {
        return false
      }
      let params = {
        username: this.userInfo.username,
        password: this.userInfo.password,
        userType: 1
      }
      this.$store
        .dispatch('login', params)
        .then(rtn => {
          if (this.userInfo.remberAccount) {
            localStorage.setItem('userInfo', JSON.stringify(this.userInfo))
          } else {
            localStorage.removeItem('userInfo')
          }
          let redirect = ''
          if (rtn.data.loginCount && rtn.data.loginCount > 1) {
            // this.userInfo = null
            redirect = decodeURIComponent(this.$route.query.r || '/')
          } else {
            redirect = '/admin/adminpassword'
          }
          this.$router.replace({
            path: redirect
          })
        })
        .catch(err => {
          this.errMessage.loginError = err.msg
        })
    },
    // 忘记密码
    forPsd() {
      this.$router.push('/forgetPsd')
    }
  }
}
</script>
<style lang="scss" scoped>
.login {
  width: 100%;
  height: 974px;
  background: url(../../assets/images/login.jpg) no-repeat;
  background-size: 100% 100%;
  .title {
    height: 60px;
    color: #f6fee6;
    font-size: 47px;
    text-align: center;
    letter-spacing: 10px;
    font-weight: bold;
    padding-top: 110px;
  }
  .errMessage {
    text-align: left;
    font-size: 14px;
    padding: 0px 45px;
    color: #ff1f1f;
  }
  .error-msg {
    color: #e92322;
    text-align: center;
    width: 100%;
  }
  .login-new {
    width: 406px;
    // height:385px;
    max-height: 420px;
    margin: 5% auto 0;
    padding-bottom: 20px;
    background-image: linear-gradient(
        rgba(0, 38, 129, 0.9),
        rgba(0, 40, 135, 0.9)
      ),
      linear-gradient(#001e72, #001e72);
    background-blend-mode: normal, normal;
    box-shadow: 0px 2px 0px 0px #0188fb, 0px -2px 0px 0px #00ccff;
    border-radius: 3px;
  }
  .user-login {
    height: 50px;
    text-align: center;
    line-height: 50px;
    font-size: 18px;
    font-weight: bold;
    color: #fff;
    text-shadow: 0px 2px 0px rgba(0, 0, 0, 0.2);
    background: linear-gradient(
        90deg,
        rgba(255, 255, 255, 0) 0%,
        rgba(2, 110, 158, 1) 50%,
        rgba(255, 255, 255, 0) 100%
      )
      no-repeat;
    background-position: 0 100%;
    background-size: 100% 2px;
  }
  .uesr-name,
  .user-psd {
    height: 60px;
    padding: 40px 0 10px;
    text-align: center;
    position: relative;
    i {
      position: absolute;
      left: 14%;
      line-height: 60px;
      font-size: 20px;
      color: #235fe2;
    }
  }
  .user-psd {
    padding: 0px 43px;
    font-size: 14px;
  }
  .uesr-name input {
    width: 253px;
    height: 42px;
    line-height: 42px;
    border: 1px solid rgba(41, 87, 159, 1);
    border-radius: 5px;
    background-color: rgba(0, 5, 46, 0.3);
    padding-left: 45px;
    margin-top: 9px;
    color: #fff;
    border-radius: 8px;
  }
  .uesr-name input:focus {
    border-color: rgba(80, 184, 244, 1);
    background-color: rgb(0, 38, 112);
    border-radius: 8px;
    outline: none;
    box-shadow: 0px 0px 10px #fff;
  }
  .uesr-name input::-webkit-input-placeholder {
    color: rgba(255, 255, 255, 0.5);
  }
  .remember-psd {
    height: 20px;
    padding: 25px 50px 10px;
    color: #999;
    font-size: 14px;
    .mm {
      a {
        color: #47c9ff;
        cursor: pointer;
        text-decoration: underline;
      }
    }
  }
  .remember-psd-sle input {
    width: 15px;
    height: 15px;
    border: 1px solid rgb(23, 81, 135);
    border-radius: 2px;
    vertical-align: middle;
    background-color: rgba(2, 48, 154, 0);
    -webkit-appearance: none;
    position: relative;
    margin: -2px 10px 0 0;
  }
  .remember-psd-sle input:checked:after {
    display: block;
    content: ' ';
    top: -1px;
    left: 3px;
    box-sizing: border-box;
    width: 6px;
    height: 12px;
    transform: rotate(45deg);
    border-width: 2px;
    border-style: solid;
    border-color: #fff;
    border-top: 0;
    border-left: 0;
    position: absolute;
  }
  .page-user-login {
    margin: 15px auto;
    width: 298px;
    height: 42px;
    line-height: 42px;
    text-align: center;
    border-radius: 4px;
    background-image: linear-gradient(-35deg, #ff9500 0%, #ffba00 100%),
      linear-gradient(0deg, #26b9fd 1%, #2069bd 100%);
    border-radius: 24px;
    border: solid 1px #ffaa00;
    font-size: 18px;
    cursor: pointer;
  }
  :-moz-placeholder {
    color: rgba(255, 255, 255, 0.5);
  }
  ::-moz-placeholder {
    color: rgba(255, 255, 255, 0.5);
  }
  :-ms-input-placeholder {
    color: rgba(255, 255, 255, 0.5);
  }
}
</style>



