import moment from './../../static/js/moment.min'
import Vue from 'vue'

Vue.filter('dateFormat', function(value, format) {
  if (!value) return '--'
  format = (format || 'YYYY-MM-DD HH:mm:ss')
  var m = moment(+value)
  var str = m.isValid() ? m.format(format) : '--'
  return str
})

Vue.filter('dateFormat2', function(val) {
  if (!val) return '--'
  let date = new Date(val)
  return (
    date.getFullYear() +
    '-' +
    (date.getMonth() + 1).toString().padStart(2, '0') +
    '-' +
    date
      .getDate()
      .toString()
      .padStart(2, '0') +
    ' ' +
    date
      .getHours()
      .toString()
      .padStart(2, '0') +
    ':' +
    date
      .getMinutes()
      .toString()
      .padStart(2, '0') +
    ':' +
    date
      .getSeconds()
      .toString()
      .padStart(2, '0')
  )
})



