import http from '../assets/js/request'

export default {
  updateSysUser(params) {
    return http.post('/user/updateSysUser', params)
  },
  userInfo(params) {
    return http.post('/user/userInfo', params)
  },
  updateSysUserZst(params) {
    return http.post('/user/updateSysUserZst', params)
  }
}



