import http from '../assets/js/request'

export default {
  developmentInstitutions(params) {
    return http.post('/developmentInstitutions/selectList', params)
  },
  developmentInstitutionsLab(params) {
    return http.post('/developmentInstitutionsLab/selectList', params)
  },
  developmentInstitutionsPlatform(params) {
    return http.post('/developmentInstitutionsPlatform/selectList', params)
  },
  developmentInstitutionsStation(params) {
    return http.post('/developmentInstitutionsStation/selectList', params)
  }
}



