import {DynamicRouters} from './dynamicRouters'
import { hasAuth } from '../assets/js/auth'

let redirects = {}
export function getRedirectMap() {
  let arr = deepCopy(DynamicRouters)
  redirects = {}
  function deepCopy(obj) {
    let newobj = obj.constructor === Array ? [] : {}
    if (typeof obj !== 'object') {
      return
    } else {
      for (let idx in obj) {
        newobj[idx] = typeof obj[idx] === 'object'
          ? deepCopy(obj[idx]) : obj[idx]
      }
    }
    return newobj
  }
  getFilter(arr)

  // 对照权限表auths,获取当前用户拥有的路由
  function getFilter(routers) {
    routers = routers.filter((route) => {
      let auth = true
      if (route.meta && route.meta.auth) {
        auth = hasAuth(route.meta.auth)
      }
      if (route.children) {
        route.children = getFilter(route.children)
      }

      return auth
    })

    return routers
  }

  // 获取父级路由重定向的路径
  function getRightRouters(routers, root) {
    routers = routers.map((route) => {
      if (route.children && route.children[0]) {
        route['isParent'] = true
        route['fullPath'] = (!root ? '/' : root) + (root && route.path !== '/' ? route.path : '')
        route['redirect'] = (!root ? '' : root) + ((root && route.path !== '/') ? route.path : '') + '/' + route.children[0].path
        redirects[route['fullPath']] = route['redirect']
        if (route['fullPath'] !== '/') { // 防止手动删除路径
          redirects[route['fullPath'] + '/'] = route['redirect']
        }

        getRightRouters(route.children, route['redirect'].substring(0, route['redirect'].length - route.children[0].path.length))
      } else {
        route['isParent'] = false
      }
    })

    return routers
  }

  getRightRouters(arr)

  return redirects
}



