import http from '../assets/js/request'

export default {
  // 获取用户权限
  getAuth() {
    return http.post('/user/menuInfo')
  },
  /**
   * 登录接口
   */
  login(params) {
    return http.post('/login', params)
  },
  /**
   *退出接口
   */
  logout() {
    return http.post('/logout')
  }
}



