import axios from 'axios'
import { Code } from './operaCode'
import router from '../../router'
import store from '../../store'
import {VueInstance} from '../../main'
let http = axios.create({
  // baseURL: window.api_host,
  headers: {
    post: {
      'Content-Type': 'application/x-www-form-urlencoded;charset=utf-8'
    }
  }
})
axios.defaults.timeout = 5000

http.interceptors.request.use(
  config => {
    let token = store.getters.token
    if (config.url === '/login') {
      return config
    }
    if (token) {
      config.headers['Authorization'] = token
    }
    return config
  },
  () => {
    return Promise.reject({
      code: Code.Error,
      msg: '操作失败！'
    })
  }
)

http.interceptors.response.use(
  res => {
    return res
  },
  err => {
    if (err.code === -101 || err.code === -102) {
      err.code = Code.Overdue
      err.msg = '用户信息已过期，请重新登录！'
    }
    if (err.message === 'Network Error') {
      return Promise.reject({
        code: Code.Offline,
        msg: '网络连接超时，请稍后重试！'
      })
    } else {
      return Promise.reject({
        code: Code.Error,
        msg: '系统错误，请稍后重试！'
      })
    }
  }
)

function successHandle(response) {
  if ((response.data && response.data.code === 1) || response.data.type === 'FeatureCollection') {
    response.errCode = 1
    return response.data
  } else {
    return Promise.reject(response.data)
  }
}

function errorHandle(err) {
  err = Object.assign(
    {
      code: Code.Error,
      msg: '操作失败！'
    },
    err
  )
  err.errCode = err.code
  if (err.code === -101 || err.code === -102) {
    err.code = Code.Overdue
    err.msg = '用户信息已过期，请重新登录！'
    // NoAuthHandle(err)
    store
      .dispatch('logout')
      .then(() => {
        router.push({ name: 'login' })
      })
      .catch(() => {
        router.push({ name: 'login' })
      })
  } else if (err.code === -104 || err.code === -106) {
    err.code = Code.NoAuth
    err.msg = '你无权限访问，请联系系统管理员！'
    NoAuthHandle(err)
  } else if (err.code === Code.Offline) {
    err.code = Code.Offline
  } else {
    err.code = Code.Error
  }
  return Promise.reject(err)
}

function NoAuthHandle(err) {
  VueInstance.$msgbox({
    title: '系统提示',
    message: '<i class="iconfont icon-cuowu"></i><span>' + err.msg + '</span>',
    center: true,
    roundButton: true,
    customClass: 'messageBox',
    type: 'success',
    dangerouslyUseHTMLString: true,
    callback: () => {
      store
        .dispatch('logout')
        .then(() => {
          router.push({ name: 'login' })
        })
        .catch(() => {
          router.push({ name: 'login' })
        })
    }
  })
}

function sendRequest(method, url, params, type) {
  let axiosConfig = {
    url: url,
    method: method,
    params: params
  }
  let regx = /static\/js/i
  if (!regx.test(url)) {
    axiosConfig.baseURL = type === 'source' ? window.api_source : window.api_host
    if (params && params.baseURL) {
      axiosConfig.baseURL = params.baseURL
    }
  }
  let response = http(axiosConfig)
  return response.then(successHandle).catch(errorHandle)
}

export default {
  get(url, params, type) {
    return sendRequest('get', url, params, type)
  },
  post(url, params, type) {
    return sendRequest('post', url, params, type)
  }
}



