// 系统设置模块路由可放在此文件下
import {AUTHS} from '../assets/js/pageAuth'

const SysManage = r => require.ensure([], () => r(require('@/view/sysManage/index')))

export default[{
  name: 'sysManage',
  path: 'sysManage',
  component: SysManage,
  meta: {
    auth: AUTHS.SysManage
  }
}]



