<template>
  <div class="leftmannage">
    <ul>
      <router-link tag="li" v-for="item in leftMannageData" v-auth="item.auth" :to="item.path" :key="item.name">
        <p>
          <i :class="item.iconClass"></i>
          <span> {{item.name}}</span>
        </p>
        <ol>
          <router-link v-for="list in item.children" tag="li" v-auth="list.auth" :to="list.chiPath" :key="list.chiName">
            <span>{{list.chiName}}</span>
          </router-link>
        </ol>
      </router-link>
    </ul>
  </div>
</template>
<script>
import { AUTHS } from '../assets/js/pageAuth'
export default {
  name: 'headerNav',
  data() {
    return {
      leftMannageData: [
        {
          name: '处室数据填报',
          iconClass: 'iconfont icon-viewgallery',
          path: '/dataManage/fillingOffice',
          auth: AUTHS.FillInRoom,
          children: [
            {
              chiName: '高新处填报',
              chiPath: '/dataManage/fillingOffice/highAndOffice',
              auth: AUTHS.HighAndOffice
            },
            {
              chiName: '科管处填报',
              chiPath: '/dataManage/fillingOffice/fillCondition',
              auth: AUTHS.FillCondition
            },
            {
              chiName: '计划处填报',
              chiPath: '/dataManage/fillingOffice/planningOffice',
              auth: AUTHS.PlanningOffice
            },
            {
              chiName: '法规处填报',
              chiPath: '/dataManage/fillingOffice/statuteOffice',
              auth: AUTHS.StatuteOffice
            },
            {
              chiName: '知产处填报',
              chiPath: '/dataManage/fillingOffice/knowledgeOffice',
              auth: AUTHS.KnowledgeOffice
            }
          ]
        },
        {
          name: '县（市）区数据审核',
          iconClass: 'iconfont icon-gerenshezhi',
          path: '/dataManage/districtCountyAudits',
          auth: AUTHS.CountyAudit,
          children: [
            {
              chiName: '企业填报数据审核',
              chiPath: '/dataManage/districtCountyAudits/enterpriseAudit',
              auth: AUTHS.EnterpriseAudit
            },
            {
              chiName: '企业填报数据汇总',
              chiPath: '/dataManage/districtCountyAudits/localListData',
              auth: AUTHS.LocalListData
            },
            {
              chiName: '县（市）区数据统计',
              chiPath: '/dataManage/districtCountyAudits/districtSummary',
              auth: AUTHS.DistrictSummary
            }
          ]
        },
        {
          name: '处室数据审核',
          iconClass: 'iconfont icon-bankuai',
          path: '/dataManage/officeReview',
          auth: AUTHS.FillInAudit,
          children: [
            {
              chiName: '高新处审核',
              chiPath: '/dataManage/officeReview/highOfficeReview',
              auth: AUTHS.HighOfficeReview
            },
            {
              chiName: '科管处审核',
              chiPath: '/dataManage/officeReview/auditOffice',
              auth: AUTHS.AuditOffice
            },
            {
              chiName: '计划处审核',
              chiPath: '/dataManage/officeReview/planningOfficeReview',
              auth: AUTHS.PlanningOfficeReview
            },
            {
              chiName: '法规处审核',
              chiPath: '/dataManage/officeReview/regulatoryReview',
              auth: AUTHS.RegulatoryReview
            },
            {
              chiName: '知产处审核',
              chiPath: '/dataManage/officeReview/knowledgeProductionAudit',
              auth: AUTHS.KnowledgeProductionAudit
            }
          ]
        },
        {
          name: '数据查看',
          iconClass: 'iconfont icon-bankuai',
          path: '/dataManage/dataView',
          auth: AUTHS.InspectionMain,
          children: [
            {
              chiName: '处室数据汇总',
              chiPath: '/dataManage/dataView/inspectionOffice',
              auth: AUTHS.InspectionOffice
            },
            {
              chiName: '全市企业数据汇总',
              chiPath: '/dataManage/dataView/dvlocalListData',
              auth: AUTHS.DvLocalListData
            },
            {
              chiName: '全市企业数据统计',
              chiPath: '/dataManage/dataView/enterpriseView',
              auth: AUTHS.EnterpriseView
            }
          ]
        }
      ]
    }
  }
}
</script>
<style lang="scss" scoped>
.leftmannage {
  width: 240px;
  padding-left: 30px;
  box-sizing: border-box;
  padding-top: 30px;
  background-color: #f8f8f8;

  ul {
    font-size: 16px;
    cursor: pointer;

    & > li:first-child {
      padding-top: 20px;
    }
    & > li {
      padding: 0 0 20px 0;
      i {
        margin-right: 5px;
        color: #197aff;
        font-weight: bolder;
        font-size: 16px;
      }
      ol {
        width: 100%;
        box-sizing: border-box;
        padding-left: 25px;
        li {
          padding: 0 0 15px 0;
          &.selected {
            color: #1559ec;
          }
        }
        li:first-child {
          padding-top: 15px;
        }
      }
    }
    li.selected > p span {
      color: #1559ec;
      font-weight: bolder;
    }
  }
}
.active {
  color: #ffcc00;
  &:after {
    content: '';
    display: block;
    height: 1px;
    margin-top: -35px;
    border-bottom: 2px solid #ffcc00;
  }
  &:hover {
    color: #ffcc00;
  }
}
</style>



