import { AUTHS } from '../assets/js/pageAuth.js'
const ObserveIndex = r =>
  require.ensure(
    [],
    () => r(require('@/view/dataObserve/index')),
    'DataObserve'
  )
const ObserveListIndex = r =>
  require.ensure(
    [],
    () => r(require('@/view/dataObserve/listIndex')),
    'DataObserve'
  )
const TechChart = r =>
  require.ensure(
    [],
    () => r(require('@/view/dataObserve/techChart')),
    'DataObserve'
  )
const IndustryDep = r =>
  require.ensure(
    [],
    () => r(require('@/view/dataObserve/industryDep')),
    'DataObserve'
  )
const InnovateFactor = r =>
  require.ensure(
    [],
    () => r(require('@/view/dataObserve/innovateFactor')),
    'DataObserve'
  )
const EntInnovate = r =>
  require.ensure(
    [],
    () => r(require('@/view/dataObserve/entInnovate')),
    'DataObserve'
  )
const PlatCarrier = r =>
  require.ensure(
    [],
    () => r(require('@/view/dataObserve/platCarrier')),
    'DataObserve'
  )
const KnowRight = r =>
  require.ensure(
    [],
    () => r(require('@/view/dataObserve/knowRight')),
    'DataObserve'
  )
const HighTech = r =>
  require.ensure(
    [],
    () => r(require('@/view/dataObserve/highTechList')),
    'DataObserve'
  )
const TechIncubator = r =>
  require.ensure(
    [],
    () => r(require('@/view/dataObserve/techIncubatorList')),
    'DataObserve'
  )
// 省级以上企业研发机构列表,索引页
const InstitutionIndex = r =>
  require.ensure(
    [],
    () => r(require('@/view/dataObserve/institutionIndex')),
    'DataObserve'
  )
// 国家、省工程技术研究中心
const ResearchCenter = r =>
  require.ensure(
    [],
    () => r(require('@/view/dataObserve/researchCenter')),
    'DataObserve'
  )
// 国家、省重点实验室
const KeyLib = r =>
  require.ensure(
    [],
    () => r(require('@/view/dataObserve/keyLib')),
    'DataObserve'
  )
// 省科技公共服务平台
const PublicPlat = r =>
  require.ensure(
    [],
    () => r(require('@/view/dataObserve/publicPlat')),
    'DataObserve'
  )
// 省院士工作站
const Workstation = r =>
  require.ensure(
    [],
    () => r(require('@/view/dataObserve/workstation')),
    'DataObserve'
  )
export default [
  {
    path: 'dataObserve',
    component: ObserveIndex,
    meta: {
      auth: AUTHS.Home
    },
    children: [
      {
        path: 'techChart',
        component: TechChart,
        meta: {
          auth: AUTHS.TechChart
        }
      },
      {
        path: 'industryDep',
        name: 'industryDep',
        component: IndustryDep,
        meta: {
          auth: AUTHS.IndustryDep
        }
      },
      {
        path: 'innovateFactor',
        name: 'innovateFactor',
        component: InnovateFactor,
        meta: {
          auth: AUTHS.InnovateFactor
        }
      },
      {
        path: 'entInnovate',
        name: 'entInnovate',
        component: EntInnovate,
        meta: {
          auth: AUTHS.EntInnovate
        }
      },
      {
        path: 'platCarrier',
        name: 'platCarrier',
        component: PlatCarrier,
        meta: {
          auth: AUTHS.PlatCarrier
        }
      },
      {
        path: 'knowRight',
        name: 'knowRight',
        component: KnowRight,
        meta: {
          auth: AUTHS.KnowRight
        }
      },
      {
        path: '',
        redirect: '/dataObserve/techChart'
      }
    ]
  },
  {
    path: 'dataObserve/list',
    component: ObserveListIndex,
    redirect: '/dataObserve',
    children: [
      {
        path: 'highTech',
        component: HighTech
      },
      {
        path: 'techIncubator',
        component: TechIncubator
      }
    ]
  },
  {
    path: 'dataObserve/list2',
    component: InstitutionIndex,
    // meta: {
    //   auth: AUTHS.InstitutionIndex
    // },
    redirect: '/dataObserve/list2/researchCenter',
    children: [
      {
        path: 'researchCenter',
        component: ResearchCenter
        // meta: {
        //   auth: AUTHS.ResearchCenter
        // }
      },
      {
        path: 'keyLib',
        component: KeyLib
        // meta: {
        //   auth: AUTHS.KeyLib
        // }
      },
      {
        path: 'publicPlat',
        component: PublicPlat
        // meta: {
        //   auth: AUTHS.PublicPlat
        // }
      },
      {
        path: 'workstation',
        component: Workstation
        // meta: {
        //   auth: AUTHS.Workstation
        // }
      }
    ]
  }
]



