import http from '../assets/js/request'

export default {
  // 数据查看-全市企业数据汇总-列表
  getListByCC2(params) {
    return http.post('/useReportInfo/getTotalListByCountyCopy2', params)
  },
  // 数据查看-全市企业数据汇总-详情
  getDetialByCC2(params) {
    return http.post('/useReportInfo/getDetailByCountyCopy2', params)
  },
  // 县市区数据审核-企业填报数据汇总-列表
  getListByCC(params) {
    return http.post('/useReportInfo/getTotalListByCountyCopy', params)
  },
  // 县市区数据审核-企业填报数据汇总-详情
  getDetialByCC(params) {
    return http.post('/useReportInfo/getDetailByCountyCopy', params)
  },
  getDetialTable(params) {
    return http.post('/dataUploadData/getTable', params)
  },
  getHeightNewList(params) {
    return http.post('/dataUploadData/selectList', params)
  },
  getFirstPageSelectList(params) {
    return http.post('/dataUpLoadController/firstPageSelectList', params)
  },
  getEditTable(params) {
    return http.post('/dataUpLoadController/getTable', params)
  },
  saveExcel(params) {
    return http.post('/dataUpLoadController/saveExcel', params)
  },
  report(params) {
    return http.post('/dataUploadBackinfo/insert', params)
  },
  // 处室数据填报－打回列表
  getReportList(params) {
    return http.post('dataUploadBackinfo/selectList', params)
  },
  // 区县数据审核列表－企业填报数据审核接口
  getReportByCounty(params) {
    return http.post('/useReportInfo/getReportByCounty', params)
  },
  //  区县数据审核列表－企业填报数据审核-详情接口
  getSelectDetail(params) {
    return http.post('/useReportInfo/selectDetail', params)
  },
  //  区县数据审核列表－企业填报数据审核-打回记录接口
  getSelectApplyList(params) {
    return http.post('/useReportInfo/selectApplyList', params)
  },
  //  区县数据审核列表－企业填报数据审核-打回接口
  getInfoapply(params) {
    return http.post('/useReportInfo/infoapply', params)
  },
  // 区县数据审核列表－区县数据汇总接口
  getTotalListByCounty(params) {
    return http.post('/useReportInfo/getTotalListByCounty', params)
  },
  // 区县数据审核列表－区县数据汇总-详情接口
  getDetailByCounty(params) {
    return http.post('/useReportInfo/getDetailByCounty', params)
  },
  // 数据查看－处室数据查看接口
  getSelectAllList(params) {
    return http.post('/dataUploadData/selectAllList', params)
  },
  // 数据查看－处室数据查看-详情接口
  getSelectAllDetail(params) {
    return http.post('/dataUploadData/selectAllDetail', params)
  },
  // 数据查看－企业数据查看接口
  getStatusNumByReport(params) {
    return http.post('/useReportInfo/getStatusNumByReport', params)
  },
  // 数据查看－企业数据查看-详情接口
  getGroupbygov(params) {
    return http.post('/useReportInfo/groupbygov_cname', params)
  },
  // 数据查看－企业数据查看-详情上报状态数量统计接口
  getStatusNum(params) {
    return http.post('/useReportInfo/getStatusNum', params)
  },
  // 处室数据审核列表
  getSelectList(params) {
    return http.post('/dataUploadData/selectList', params)
  },
  // 处室数据审核-详情
  getGetTable(params) {
    return http.post('/dataUploadData/getTable', params)
  }
}



