// 信息模块
import { AUTHS } from '../assets/js/pageAuth'
const Information = r =>
  require.ensure(
    [],
    () => r(require('@/view/information/information')),
    'information'
  )
const infoPolicy = r =>
  require.ensure(
    [],
    () => r(require('@/view/information/infoPolicy')),
    'information'
  )
const infoPolicyAdd = r =>
  require.ensure(
    [],
    () => r(require('@/view/information/infoPolicyAdd')),
    'information'
  )
const infoPolicyEdit = r =>
  require.ensure(
    [],
    () => r(require('@/view/information/infoPolicyEdit')),
    'information'
  )
const infoPolicyView = r =>
  require.ensure(
    [],
    () => r(require('@/view/information/infoPolicyView')),
    'information'
  )
const scienceTrend = r =>
  require.ensure(
    [],
    () => r(require('@/view/information/scienceTrend')),
    'information'
  )
const scienceTrendAdd = r =>
  require.ensure(
    [],
    () => r(require('@/view/information/scienceTrendAdd')),
    'information'
  )
const scienceTrendDetial = r =>
  require.ensure(
    [],
    () => r(require('@/view/information/scienceTrendDetial')),
    'information'
  )

const infoNotice = r =>
  require.ensure(
    [],
    () => r(require('@/view/information/infoNotice')),
    'information'
  )
const infoNoticeAdd = r =>
  require.ensure(
    [],
    () => r(require('@/view/information/infoNoticeAdd')),
    'information'
  )
export default [
  {
    path: 'information',
    component: Information,
    meta: {
      auth: AUTHS.InfoManage
    },
    children: [
      {
        name: 'infoNotice',
        path: 'infoNotice',
        component: infoNotice,
        meta: {
          auth: AUTHS.NoticeManage
        }
      },
      {
        name: 'scienceTrend',
        path: 'scienceTrend',
        component: scienceTrend,
        meta: {
          auth: AUTHS.TechDynamic
        }
      },
      {
        name: 'infoPolicy',
        path: 'infopolicy',
        component: infoPolicy,
        meta: {
          auth: AUTHS.PolicyManage
        }
      }
    ]
  },
  {
    name: 'infoNoticeAdd',
    path: 'information/infoNoticeAdd',
    component: infoNoticeAdd
  },
  {
    name: 'infoPolicyAdd',
    path: 'information/infopolicyadd',
    component: infoPolicyAdd
  },
  {
    name: 'infoPolicyEdit',
    path: 'information/infopolicyedit/:id',
    component: infoPolicyEdit
  },
  {
    name: 'infoPolicyView',
    path: 'information/infopolicyview/:id',
    component: infoPolicyView
  },
  {
    name: 'scienceTrendAdd',
    path: 'information/scienceTrendAdd',
    component: scienceTrendAdd
  },
  {
    name: 'scienceTrendDetial',
    path: 'information/scienceTrendDetial',
    component: scienceTrendDetial
  }
]



