/* eslint-disable */
import http from '../assets/js/request'

export default {
    // 通知公告相关接口
    noticeList(params) {
        return http.post('/notice/selectList', params)
    },
    noticeAdd(params) {
        return http.post('/notice/insert', params)
    },
    // 通知公告修改状态
    noticeUpdateStatus(params) {
        return http.post('/notice/updateStatus', params)
    },
    // 通知公告修改状态
    noticeDelete(params) {
        return http.post('/notice/delete', params)
    },
    // 通知公告状态
    noticeUpdate(params) {
        return http.post('/notice/update', params)
    },
    // 通知公告详情
    noticeDetail(params) {
        return http.post('/notice/selectDetail', params)
    },
    noticeShowStatue(params) {
        return http.post('/notice/showStatue', params)
    }
}


