<template>
  <div class="login">
    <div class="title">徐州科技大数据运营平台</div>
     <form>
      <div class="login-new">
        <div class="user-login">忘记密码</div>
        <div class="uesr-name">
          <div>忘记密码请拨打:</div>
          <div class="phone">0516-83852410</div>
          <div>联系科技局管理员进行密码找回</div>
        </div>
        <div class="page-user-login" @click="confirm">确&nbsp;&nbsp;&nbsp;&nbsp;定</div>
      </div>
    </form>
  </div>
</template>
<script>
export default {
  data() {
    return {}
  },
  methods: {
    confirm() {
      this.$router.push('/login')
    }
  }
}
</script>
<style lang="scss" scoped>
.login {
  width: 100%;
  height: 974px;
  background: url(../../assets/images/login.jpg) no-repeat;
  background-size: 100% 100%;
  .title {
    height: 60px;
    color: #f6fee6;
    font-size: 47px;
    text-align: center;
    letter-spacing: 10px;
    font-weight: bold;
    padding-top: 110px;
  }
  .login-new {
    width: 390px;
    height: 365px;
    margin: 5% auto 0;
    background-image: linear-gradient(
        rgba(0, 38, 129, 0.9),
        rgba(0, 40, 135, 0.9)
      ),
      linear-gradient(#001e72, #001e72);
    background-blend-mode: normal, normal;
    box-shadow: 0px 2px 0px 0px #0188fb, 0px -2px 0px 0px #00ccff;
    border-radius: 3px;
  }

  .user-login {
    height: 50px;
    text-align: center;
    line-height: 50px;
    font-size: 18px;
    font-weight: bold;
    color: #fff;
    text-shadow: 0px 2px 0px rgba(0, 0, 0, 0.2);
    background: linear-gradient(
        90deg,
        rgba(255, 255, 255, 0) 0%,
        rgba(2, 110, 158, 1) 50%,
        rgba(255, 255, 255, 0) 100%
      )
      no-repeat;
    background-position: 0 100%;
    background-size: 100% 2px;
  }

  .uesr-name {
    padding: 40px 0 10px;
    text-align: center;
    div {
      line-height: 45px;
      font-size: 16px;
      color: #fff;
    }
    .phone {
      font-size: 30px;
      font-weight: bold;
      color: #47c9ff;
    }
  }
  .page-user-login {
    margin: 45px auto;
    width: 298px;
    height: 42px;
    line-height: 42px;
    text-align: center;
    border-radius: 4px;
    background-image: linear-gradient(-35deg, #ff9500 0%, #ffba00 100%),
      linear-gradient(0deg, #26b9fd 1%, #2069bd 100%);
    border-radius: 24px;
    border: solid 1px #ffaa00;
    font-size: 18px;
    cursor: pointer;
  }
}
</style>



