import * as types from './mutation-types'

function mapEnum(enums, t) {
  return enums.filter(e => e.type === t)
}

export default {
  [types.COM_ENUM](state, data) {
    state.enums = data.map(d => {
      return {
        name: d.showName,
        value: d.dictionaryId,
        type: d.type
      }
    })
    // 1,区域
    state.areas = mapEnum(state.enums, 1).reverse()
    // 2,领域大类
    state.categories = mapEnum(state.enums, 2).reverse()
    // 3,类型(使用页面:省级及以上科技孵化器及众创空间数量)
    state.types = mapEnum(state.enums, 8).reverse()
    // 4,级别(使用页面:省级及以上科技孵化器及众创空间数量)
    state.levels = mapEnum(state.enums, 4).reverse()
  }
}



