import Vue from 'vue'

Vue.filter('numConvert', function(value, req = '-', req2 = '') {
  if (value) {
    if (req === '-' || value === '-') { return value } else {
      value = value > 0 ? value : -value
      return value + req
    }
  }
  if (value === 0) {
    return !req2 ? value : req2
  }
  return ''
})



