<template>
 <div v-if="backlist.length>0">
    <div class="j-title">打回记录</div>
    <div class="back-info" v-for="item in backlist" :key="item.id">
       <p class="i-tit">{{item.reason}}</p>
       <p class="i-time">操作人：<span v-if="item.optUser">{{item.optUser}}</span><span v-else>-</span>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;{{item.optDate | dateFormat}}</p>
    </div>
  </div>
</template>
<script>
export default {
  name: 'jBackList',
  data() {
    return {}
  },
  props: ['backlist']
}
</script>
<style lang="scss" scoped>
    .back-info{
      padding:30px 130px;
      background: #edf3ff;
      margin-bottom: 10px;
      &:last-child{
        margin-bottom:30px;
      }
      .i-tit{
        font-weight: bolder;
        margin-bottom: 20px;
        word-break:break-all;
        color:#333

      }
      .i-time{
        text-align: right;
        color:#7c7d7d
      }
    }
    .j-title{
        padding-left: 10px;
        height: 16px;
        line-height: 16px;
        background: url(~@/assets/images/t-tit.jpg) no-repeat;
        margin-bottom: 30px;
        font-size:16px;
    }
</style>



