import { AUTHS } from '../assets/js/pageAuth'
// 通知公告模块路由可放在此文件下
const DataManage = r =>
  require.ensure(
    [],
    () => r(require('@/view/dataManage/dataManage')),
    'dataManage'
  )
const HighAndOffice = r =>
  require.ensure(
    [],
    () => r(require('@/view/dataManage/fillingOffice/highAndOffice')),
    'dataManage'
  )
const FillCondition = r =>
  require.ensure(
    [],
    () => r(require('@/view/dataManage/fillingOffice/fillCondition')),
    'dataManage'
  )
const PlanningOffice = r =>
  require.ensure(
    [],
    () => r(require('@/view/dataManage/fillingOffice/planningOffice')),
    'dataManage'
  )
const StatuteOffice = r =>
  require.ensure(
    [],
    () => r(require('@/view/dataManage/fillingOffice/statuteOffice')),
    'dataManage'
  )
const KnowledgeOffice = r =>
  require.ensure(
    [],
    () => r(require('@/view/dataManage/fillingOffice/knowledgeOffice')),
    'dataManage'
  )
const EnterpriseAudit = r =>
  require.ensure(
    [],
    () => r(require('@/view/dataManage/districtCountyAudits/enterpriseAudit')),
    'dataManage'
  )
const DistrictSummary = r =>
  require.ensure(
    [],
    () => r(require('@/view/dataManage/districtCountyAudits/districtSummary')),
    'dataManage'
  )
const LocalListData = r =>
  require.ensure(
    [],
    () => r(require('@/view/dataManage/districtCountyAudits/localListData')),
    'dataManage'
  )
const HighOfficeReview = r =>
  require.ensure(
    [],
    () => r(require('@/view/dataManage/officeReview/highOfficeReview')),
    'dataManage'
  )
const AuditOffice = r =>
  require.ensure(
    [],
    () => r(require('@/view/dataManage/officeReview/auditOffice')),
    'dataManage'
  )
const PlanningOfficeReview = r =>
  require.ensure(
    [],
    () => r(require('@/view/dataManage/officeReview/planningOfficeReview')),
    'dataManage'
  )
const RegulatoryReview = r =>
  require.ensure(
    [],
    () => r(require('@/view/dataManage/officeReview/regulatoryReview')),
    'dataManage'
  )
const KnowledgeProductionAudit = r =>
  require.ensure(
    [],
    () => r(require('@/view/dataManage/officeReview/knowledgeProductionAudit')),
    'dataManage'
  )
const InspectionOffice = r =>
  require.ensure(
    [],
    () => r(require('@/view/dataManage/dataView/inspectionOffice')),
    'dataManage'
  )
const DvLocalListData = r =>
  require.ensure(
    [],
    () => r(require('@/view/dataManage/dataView/localListData')),
    'dataManage'
  )
const DvLocalViewData = r =>
  require.ensure(
    [],
    () => r(require('@/view/dataManage/dataView/localViewData')),
    'dataManage'
  )
const EnterpriseView = r =>
  require.ensure(
    [],
    () => r(require('@/view/dataManage/dataView/enterpriseView')),
    'dataManage'
  )
const EnterpriseViewDetail = r =>
  require.ensure(
    [],
    () => r(require('@/view/dataManage/dataView/enterpriseViewDetail')),
    'dataManage'
  )
const InspectionOfficeDetail = r =>
  require.ensure([], () =>
    r(require('@/view/dataManage/dataView/inspectionOfficeDetail')), 'dataManage'
  )
const EditIndex = r =>
  require.ensure(
    [],
    () => r(require('@/view/dataManage/editIndex')),
    'dataManage'
  )
const EditHighAndOffice = r =>
  require.ensure(
    [],
    () => r(require('@/view/dataManage/fillingOffice/editHighAndOffice')),
    'dataManage'
  )
const EditFillCondition = r =>
  require.ensure(
    [],
    () => r(require('@/view/dataManage/fillingOffice/editFillCondition')),
    'dataManage'
  )
const EditKnowledgeOffice = r =>
  require.ensure(
    [],
    () => r(require('@/view/dataManage/fillingOffice/editKnowledgeOffice')),
    'dataManage'
  )
const EditPlanningOffice = r =>
  require.ensure(
    [],
    () => r(require('@/view/dataManage/fillingOffice/editPlanningOffice')),
    'dataManage'
  )
const EditStatuteOffice = r =>
  require.ensure(
    [],
    () => r(require('@/view/dataManage/fillingOffice/editStatuteOffice')),
    'dataManage'
  )

const DataAuditView = r =>
  require.ensure(
    [],
    () => r(require('@/view/dataManage/districtCountyAudits/dataAuditView')),
    'dataManage'
  )

const LocalViewData = r =>
  require.ensure(
    [],
    () => r(require('@/view/dataManage/districtCountyAudits/localViewData')),
    'dataManage'
  )

const DistrictSummaryDetail = r =>
  require.ensure([], () =>
    r(require('@/view/dataManage/districtCountyAudits/districtSummaryDetail')), 'dataManage'
  )

const regulatoryDetail = r =>
  require.ensure([], () =>
    r(require('@/view/dataManage/officeReview/regulatoryDetail')), 'dataManage'
  )

const knowledgeDetail = r =>
  require.ensure([], () =>
    r(require('@/view/dataManage/officeReview/knowledgeDetail')), 'dataManage'
  )

const HighAndOfficeDetail = r =>
  require.ensure([], () =>
    r(require('@/view/dataManage/officeReview/highAndOfficeDetail')), 'dataManage'
  )
const AuditOfficeDetail = r =>
  require.ensure([], () =>
    r(require('@/view/dataManage/officeReview/auditOfficeDetail')), 'dataManage'
  )
const PlanningOfficeReviewDetail = r =>
  require.ensure([], () =>
    r(require('@/view/dataManage/officeReview/planningOfficeReviewDetail')), 'dataManage'
  )
export default [
  {
    name: 'dataManage',
    path: 'dataManage',
    component: DataManage,
    // redirect: '/dataManage/fillingOffice/highAndOffice',
    meta: {
      auth: AUTHS.DataReport
    },
    children: [
      //  处室数据填报
      {
        path: 'fillingOffice',
        meta: {
          auth: AUTHS.FillInRoom
        }
        // redirect: '/dataManage/fillingOffice/highAndOffice'
      },
      {
        path: 'fillingOffice/highAndOffice',
        component: HighAndOffice,
        meta: {
          auth: AUTHS.HighAndOffice
        }
      },
      {
        path: 'fillingOffice/fillCondition',
        component: FillCondition,
        meta: {
          auth: AUTHS.FillCondition
        }
      },
      {
        path: 'fillingOffice/planningOffice',
        component: PlanningOffice,
        meta: {
          auth: AUTHS.PlanningOffice
        }
      },
      {
        path: 'fillingOffice/statuteOffice',
        component: StatuteOffice,
        meta: {
          auth: AUTHS.StatuteOffice
        }
      },
      {
        path: 'fillingOffice/knowledgeOffice',
        component: KnowledgeOffice,
        meta: {
          auth: AUTHS.KnowledgeOffice
        }
      },
      //  区县数据审核
      {
        path: 'districtCountyAudits',
        meta: {
          auth: AUTHS.CountyAudit
        }
        // redirect: '/dataManage/districtCountyAudits/enterpriseAudit'
      },
      {
        path: 'districtCountyAudits/enterpriseAudit',
        component: EnterpriseAudit,
        meta: {
          auth: AUTHS.EnterpriseAudit
        }
      },
      {
        path: 'districtCountyAudits/localListData',
        component: LocalListData,
        meta: {
          auth: AUTHS.LocalListData
        }
      },
      {
        path: 'districtCountyAudits/districtSummary',
        component: DistrictSummary,
        meta: {
          auth: AUTHS.DistrictSummary
        }
      },
      //  处室数据审核
      {
        path: 'officeReview',
        meta: {
          auth: AUTHS.FillInAudit
        }
        // redirect: '/dataManage/officeReview/highOfficeReview'
      },
      {
        path: 'officeReview/highOfficeReview',
        component: HighOfficeReview,
        meta: {
          auth: AUTHS.HighOfficeReview
        }
      },
      {
        path: 'officeReview/auditOffice',
        component: AuditOffice,
        meta: {
          auth: AUTHS.AuditOffice
        }
      },
      {
        path: 'officeReview/planningOfficeReview',
        component: PlanningOfficeReview,
        meta: {
          auth: AUTHS.PlanningOfficeReview
        }
      },
      {
        path: 'officeReview/regulatoryReview',
        component: RegulatoryReview,
        meta: {
          auth: AUTHS.RegulatoryReview
        }
      },
      {
        path: 'officeReview/knowledgeProductionAudit',
        component: KnowledgeProductionAudit,
        meta: {
          auth: AUTHS.KnowledgeProductionAudit
        }
      },
      //  数据查看
      {
        path: 'dataView',
        meta: {
          auth: AUTHS.InspectionMain
        }
        // redirect: '/dataManage/dataView/inspectionOffice'
      },
      {
        path: 'dataView/inspectionOffice',
        component: InspectionOffice,
        meta: {
          auth: AUTHS.InspectionOffice
        }
      },
      {
        path: 'dataView/dvlocalListData',
        component: DvLocalListData,
        meta: {
          auth: AUTHS.DvLocalListData
        }
      },
      {
        path: 'dataView/enterpriseView',
        component: EnterpriseView,
        meta: {
          auth: AUTHS.EnterpriseView
        }
      }
    ]
  },
  {
    path: 'dataManage/edit',
    component: EditIndex,
    children: [
      {
        path: 'highAndOffice',
        component: EditHighAndOffice
      },
      {
        path: 'fillCondition',
        component: EditFillCondition
      },
      {
        path: 'knowledgeOffice',
        component: EditKnowledgeOffice
      },
      {
        path: 'planningOffice',
        component: EditPlanningOffice
      },
      {
        path: 'statuteOffice',
        component: EditStatuteOffice
      },
      // 区县数据审核-企业填报数据审核-查看详情页
      {
        name: 'DataAuditView',
        path: 'enterpriseAudit',
        component: DataAuditView
      },
      // 区县数据审核-区县数据汇总-查看详情页
      {
        name: 'districtSummaryDetail',
        path: 'districtSummaryDetail',
        component: DistrictSummaryDetail
      },
      // 区县数据审核-企业填报数据汇总-查看详情页
      {
        name: 'localViewData',
        path: 'localviewdata/:title',
        component: LocalViewData
      }
    ]
  },
  {
    path: 'dataManage/look',
    component: EditIndex,
    children: [
      {
        path: 'enterViewDetail',
        component: EnterpriseViewDetail
      },
      {
        path: 'inspectionOfficeDetail',
        component: InspectionOfficeDetail
      },
      {
        name: 'DvlocalViewData',
        path: 'dvlocalViewData/:title',
        component: DvLocalViewData
      },
      {
        path: 'regulatorydetail',
        name: 'regulatoryDetail',
        component: regulatoryDetail
      },
      {
        path: 'knowledgedetail',
        name: 'knowledgeDetail',
        component: knowledgeDetail
      },
      //  高新处审核
      {
        path: 'highAndOfficeDetail',
        component: HighAndOfficeDetail
      },
      {
        path: 'auditOfficeDetail',
        component: AuditOfficeDetail
      },
      {
        path: 'planningOfficeReviewDetail',
        component: PlanningOfficeReviewDetail
      }
    ]
  }
]



