import Vue from 'vue'
import Router from 'vue-router'

import Layout from '@/view/layout'
import Login from '@/view/login/login'
import ForgetPsd from '@/view/login/forgetPsd'
import notfound from '@/view/errPage/404'
import DataObserve from './dataObserve'
import Information from './information'
import DataManage from './dataManage'
import SysManage from './sysManage'
import Admin from './admin'
import store from '../store'
import {getRedirectMap} from './getRedirectMap'
import {hasAuth} from '../assets/js/auth'
Vue.use(Router)
const constantRouterMap = [
  {
    name: 'login',
    path: '/login',
    component: Login,
    meta: {
      requireAuth: false // 添加该字段，表示进入这个路由是不需要登录的
    }
  },
  {
    name: 'forgetPsd',
    path: '/forgetPsd',
    component: ForgetPsd,
    meta: {
      requireAuth: false // 添加该字段，表示进入这个路由是不需要登录的
    }
  },
  {
    name: 'notfound',
    path: '/404',
    component: notfound,
    meta: {
      requireAuth: false // 添加该字段，表示进入这个路由是不需要登录的
    }
  }
]
export const notfoundRouter = {
  path: '**',
  redirect: '/404',
  meta: {
    requireAuth: false
  }
}

export const dynamicRouters = [
  {
    path: '/',
    component: Layout,
    children: [...DataObserve, ...Information, ...DataManage, ...SysManage, ...Admin]
  }
]
let router = new Router({
  routes: [
    ...constantRouterMap,
    ...dynamicRouters,
    notfoundRouter
  ],
  linkActiveClass: 'selected'
})
store.dispatch('refresh')
let redirects = {}

router.beforeEach((to, from, next) => {
  let isLogin = store.getters.isLogin || to.meta.requireAuth === false
  if (isLogin) {
    redirects = getRedirectMap()

    let auth = true
    if (to.meta && to.meta.auth) {
      let authKey = to.meta.auth
      if (Object.prototype.toString.apply(authKey) === '[object Function]') {
        authKey = authKey(to)
      }
      auth = hasAuth(authKey)
    }
    if (auth) {
      if (redirects[to.fullPath]) {
        next({path: redirects[to.fullPath]})
      } else {
        next()
      }
    } else {
      next({path: '/404'})
    }
  } else {
    next({
      path: '/login'
    })
  }
})

export default router



