<template>
  <div class="layout">
    <jHeaderNav></jHeaderNav>
    <div class="main">
      <keep-alive>
        <router-view v-if="$route.meta && $route.meta.keepAlive"></router-view>
      </keep-alive>
      <router-view v-if="!$route.meta || !$route.meta.keepAlive"></router-view>
    </div>
    <footer class="bottom">
      <p class="news">主办单位：徐州市科学技术局©1997-2018维护：徐州市科技信息网络中心联系方式</p>
      <p>徐州市科学技术局 版权所有 ICP备案编号:苏ICP备15039495号-1网站标识码：3203000037</p>
    </footer>
  </div>
</template>
<script>
import { jHeaderNav } from './../components'
export default {
  name: 'layout',
  components: { jHeaderNav },
  data() {
    return {
    }
  },
  created() {
    this.$store.dispatch('init')
  }
}
</script>
<style lang="scss" scoped>
.main {
  width: 1280px;
  margin: 0 auto 20px;
  min-height: calc(100vh - 110px - 100px - 21px);
  background: #fff;
  box-shadow: 0 2px 8px #e2d7d7;
  border-radius: 5px;
  overflow:hidden;
}
@media (max-height: 820px) {
  .main {
    min-height: 600px;
  }
}
.bottom {
  width: 100%;
  height: 100px;
  min-width: 1280px;
  text-align: center;
  background: #303030;
  color: #666;
  p:nth-child(1) {
    padding-top: 30px;
  }
  p:nth-child(2) {
    padding-top: 10px;
  }
}
</style>



