<template>
  <div class="header">
    <div class="title">
      <img class="fl" src="../assets/images/logo.png" />
      <router-link tag="div" :to="{path:'/dataObserve'}" class="headline fl">
        <span class="title_top fl">徐州科技大数据运营平台</span>
        <span>XUZHOU SCIENCE AND TECHNOLOGY BIG DATA PLATFORM</span>
      </router-link>
    </div>
    <ul class="fl">
      <router-link v-for="item in menus" tag="li" v-auth="item.auth" :key="item.name" active-class="active" :to="item.path">
        {{item.name}}
      </router-link>
      <router-link to="" tag="li" @click.native="enterSysManage" v-auth="AUTHS.SysManage">
        <form method="post" class="user-form" id="userForm" :action="actionUri">
        <input type="text" name="username" id="username" placeholder="用户名" :value="userName">
        <input type="password" name="password" id="password" placeholder="密码" :value="psw">
      </form>
        系统管理</router-link>
    </ul>
    <div class="userinfo fr">
      <div class="news username fl" :title="realName" @click="jumpUserInfo">
        <i class="iconfont icon-icon-login"></i>{{realName}}</div>
      <div class="news fr" @click="logout">退出</div>
    </div>

  </div>
</template>
<script>
import { AUTHS } from '../assets/js/pageAuth'
export default {
  name: 'headerNav',
  data() {
    return {
      realName: this.$store.state.user.userInfo.realName,
      //      userName: this.$store.state.user.userInfo.userName,
      //      psw: this.$store.state.user.userInfo.psw,
      userName: this.$store.state.user.userInfo.userName,
      psw: this.$store.state.user.userInfo.tpsw,
      menus: [
        { name: '数据监测', path: '/dataObserve', auth: AUTHS.Home },
        { name: '信息管理', path: '/information', auth: AUTHS.InfoManage },
        { name: '数据上报管理', path: '/dataManage', auth: AUTHS.DataReport }
        //        { name: '系统管理', path: '/sysManage', auth: AUTHS.SysManage }
      ],
      // actionUri: 'http://' + window.location.host + '/login'
      actionUri: window.host + '/login',
      AUTHS: AUTHS
    }
  },
  methods: {
    enterSysManage() {
      document.getElementById('userForm').submit()
    },
    logout() {
      this.$store.dispatch('logout').then(rtn => {
        this.$router.push('/login')
      })
    },
    jumpUserInfo() {
      this.$router.push('/admin')
    }
  }
}
</script>
<style lang="scss" scoped>
.header {
  color: #fff;
  width: 1280px;
  min-width: 1280px;
  margin: 0 auto;
  display: flex;
  .headline {
    cursor: pointer;
    .title_top {
      width:100%;
      font-size: 30px;
      color: #f0f0f0;
      margin-top: 30px;
      font-weight: bold;
      letter-spacing: 2px;
    }
    span {
      display: block;
    }
    span:nth-child(2) {
      font-size: 12px;
    }
  }
  .title {
    width: 38%;
    height: 110px;
    display: flex;
    img {
      margin-top: 30px;
      width: 67px;
      height: 50px;
      margin-right: 7px;
    }
    .title_top a:hover {
      text-decoration: none;
    }
  }
  ul {
    width: 50%;
    padding: 0 0 0 45px;
    font-size: 16px;
    line-height: 110px;
    box-sizing: border-box;
    li {
      padding: 0 18px;
      cursor: pointer;
      float: left;
    }
  }
  .userinfo {
    width: 11%;
    .news {
      cursor: pointer;
      color: #accaff;
      font-size: 14px;
      line-height: 110px;
    }
    .username {
      width: 76px;
      overflow: hidden;
      white-space: nowrap;
      text-overflow: ellipsis;
      padding-right: 20px;
    }
  }
}
.active {
  color: #ffcc00;
  &:after {
    content: '';
    display: block;
    height: 1px;
    margin-top: -35px;
    border-bottom: 2px solid #ffcc00;
  }
  &:hover {
    color: #ffcc00;
  }
}
.user-form {
  width: 1px;
  height: 1px;
  visibility: hidden;
  input {
    visibility: hidden;
  }
}
</style>



