import { login as api } from '../../api'
import * as types from '../mutation-types'

const state = {
  userInfo: {},
  latest: '',
  auths: {}
}

const getters = {
  token(state) {
    return state.userInfo.token
  },
  isLogin(state) {
    return !!state.userInfo.token
  }
}

const actions = {
  /**
   * 用户登录
   */
  async login({ commit, state, dispatch }, params) {
    let rtn = await api.login({
      username: params.username,
      password: params.password,
      userType: params.userType
    })
    if (rtn.code === 1) {
      var user = Object.assign(rtn.data, {userName: params.username, tpsw: params.password})
      commit(types.USER_INFO, user)
      // 登录成功后获取用户权限
      rtn = await dispatch('getUserAuth')
    }
    return rtn
  },
  async logout({ commit, state }) {
    let rtn = await api.logout()
    commit(types.USER_INFO, {})
    commit(types.USER_AUTH, {})
    return rtn
  },
  /**
   * 获取用户权限
   */
  async getUserAuth({ commit, state }, params) {
    let rtn = await api.getAuth({
      token: state.userInfo.token
    })
    if (rtn.code === 1) {
      var auths = {}
      rtn.data.buttonList &&
        rtn.data.buttonList.forEach(btn => {
          auths[btn.moduleMark] = {
            name: btn.moduleName,
            type: 'button'
          }
        })
      rtn.data.menuList &&
        rtn.data.menuList.forEach(menu => {
          auths[menu.moduleMark] = {
            name: menu.moduleName,
            type: 'menu'
          }
        })

      auths['menu_admin_main'] = 'menu_admin_main'
      commit(types.USER_AUTH, auths)
    }
    rtn.data['loginCount'] = state.userInfo.loginCount
    return rtn
  }
}

const mutations = {
  [types.USER_INFO](state, user) {
    state.userInfo = user
    setLocalStorage('muser', user)
  },
  [types.USER_AUTH](state, auths) {
    state.auths = auths
    setLocalStorage('mauths', auths)
  }
}

function setLocalStorage(key, value) {
  try { // 防止无痕浏览器下无法使用storage
    if (value) {
      localStorage.setItem(key, JSON.stringify(value))
    } else {
      localStorage.removeItem(key)
    }
  } catch (e) {
    console.info(e)
  }
}

export default {
  state,
  actions,
  getters,
  mutations
}



