import Vue from 'vue'

Vue.filter('numLine', function(value, unit = '-') {
  if (value) {
    if (unit === '-') { return value } else {
      value = value > 0 ? value : -value
      return value + unit
    }
  }
  if (value === 0 || (value === '' && unit !== '-')) {
    return value
  }
  return '' + unit
})



